import ModalContainer from "../../components/ModalContainer";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup"
import { useState } from "react";
import IconButton from "../../components/IconButton";
import {BiTrash} from "@react-icons/all-files/bi/BiTrash"
import BgLoader from "../../components/BgLoader";
import { useDispatch } from "react-redux";
import { post } from "../../application/middlewares";
import toast from "react-hot-toast";

function Form({visible, title, setCloseModal, values, form, token}){
    const {register,  formState:{errors}, handleSubmit, reset} = useForm({
        resolver:yupResolver(schema)
    })
    const [del_img, setDelImg] = useState(false)
    const [image, setImage] = useState()
    const [selectedFile, setSelectedFile] = useState()
    const [loading, setLoading] = useState()
    const onChangeHandler = (e) =>{
        setSelectedFile(e.target.files[0])
        setImage(URL.createObjectURL(e.target.files[0]))
    }
    const dispatch = useDispatch();

    const onSubmit = (data) =>{
        dispatch(post({
            url:`api/admin/add-news`,
            data,
            token,
            action : (response) =>{
                if(response.success){
                    // toast.success("Added succesfully")
                }else{
                    // toast.error("Не добавлено")
                }
            }
        }))    
    }

    return (
        <ModalContainer size = "md"
            visible = {visible}
            title = {title}
            setCloseModal = {()=>{
                setCloseModal();
                reset({})
            }}
        >
            <form onSubmit = {handleSubmit(onSubmit)}>
                <div className = "flex flex-row">
                    <div className = "w-1/3 px-2">
                        <div className="relative w-full mb-6">
                            <label>Название на туркменском</label>
                            <input type="text" {...register("title_tm")}
                                autoComplete="false" name = "title_tm"
                                className={`${errors.title_tm ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="Max-Deluxe"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.title_tm?.message}
                            </p>
                        </div>
                        <div className="flex flex-col w-full  relative mb-6 p-2">
                            <label>Описание на туркменском</label>
                            <textarea type="text" {...register("article_tm")} name="article_tm"
                                autoComplete="false"
                                className={`${errors.article_tm ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-40 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="описание на русском"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.article_tm?.message}
                            </p>
                        </div>
                    </div>
                    <div className = "w-1/3 px-2">
                        <div className="relative w-full mb-6">
                            <label>Название на русском</label>
                            <input type="text" {...register("title_ru")}
                                autoComplete="false" name = "title_ru"
                                className={`${errors.title_ru ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="Max-Deluxe"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.title_ru?.message}
                            </p>
                        </div>
                        <div className="flex flex-col w-full h-48 relative mb-6 p-2">
                            <label>Описание на русском</label>
                            <textarea type="text" {...register("article_ru")} name="article_ru"
                                autoComplete="false"
                                className={`${errors.article_ru ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-60 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="описание на русском"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.article_ru?.message}
                            </p>
                        </div>
                    </div>
                    <div className = "w-1/3 px-2">
                        <div className="relative w-full mb-6">
                            <label>Название на английском</label>
                            <input type="text" {...register("title_en")}
                                autoComplete="false" name = "title_en"
                                className={`${errors.title_en ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="Max-Deluxe"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.title_en?.message}
                            </p>
                        </div>
                        <div className="flex flex-col w-full h-48 relative mb-6 p-2">
                            <label>Описание на английском</label>
                            <textarea type="text" {...register("article_en")} name="article_en"
                                autoComplete="false"
                                className={`${errors.article_en ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-60 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="описание на русском"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.article_en?.message}
                            </p>
                        </div>
                    </div>
                </div>
                <div className = "flex flex-row h-48">
                    {
                        (form === "add" || !values?.destination || del_img) ? 
                        <div className = "w-1/2 h-12 pt-4 flex flex-col justify-center items-center">
                            <div className = "p-2">
                                <label>Выберите файл</label>
                                <input type = "file" name = "picture" className = "px-3 py-5" onChange = {(e)=>onChangeHandler(e)} />
                            </div>
                            <div className = "w-96 h-full flex justify-center items-center">
                            {image ? 
                                <img className = "pb-3 px-3 h-32 w-auto" alt = "Hello world" src = {image}/>
                            :
                            <>
                            </>
                            }
                            </div>
                        </div>
                        : form === "update" ?
                            <div className = "flex flex-row relative justify-center items-center w-96 h-full p-1 px-5">
                                <div className = "absolute right-0 top-0 rounded-full">
                                    <IconButton tooltip = "Удалить" handleClick = {()=>setDelImg(true)} icon={<BiTrash className="text-2xl "/>}/>
                                </div>
                                <img className = "object-contain rounded-md " src={`${process.env.REACT_APP_FILE_URL}/${values?.destination}-big.webp`} alt="CTYPES"/>
                            </div>
                        : <></>
                    }
                </div>
                <div className= "w-full flex justify-center items-center">
                <button type="submit"  disabled = {loading} className="w-48 flex remove-button-bg justify-center items-center px-4 h-10 text-white transform ease-in-out duration-300 hover:scale-110 active:scale-100 font-semibold text-base rounded-full bg-green-500 hover:bg-green-400 active:bg-green-500 focus:outline-none shadow-md">
                   {loading ? 
                   <BgLoader loading = {loading}/>
                       :
                       <>{form === "add" ? "Добавить" :form === "update" ? "Обновить" : form === "sale" ? "Добавить скидку": ""}</>
                   } 
                </button>
            </div>
                
            </form>
        </ModalContainer>
    )
}

const schema = Yup.object().shape({
    title_tm:Yup.string("Должна быть строка").required("Обязательное поле").min(5, "Минимум 5 элементов").max(150, "Максимум 150 элемеентов"),
    article_tm:Yup.string("Должна быть строка").required("Обязательное поле").min(15, "Минимум 50 элементов").max(1000, "Максимум 1000 элемеентов"),
    title_ru:Yup.string("Должна быть строка").required("Обязательное поле").min(5, "Минимум 5 элементов").max(150, "Максимум 150 элемеентов"),
    article_ru:Yup.string("Должна быть строка").required("Обязательное поле").min(15, "Минимум 50 элементов").max(1000, "Максимум 1000 элемеентов"),
    title_en:Yup.string("Должна быть строка").required("Обязательное поле").min(5, "Минимум 5 элементов").max(150, "Максимум 150 элемеентов"),
    article_en:Yup.string("Должна быть строка").required("Обязательное поле").min(15, "Минимум 50 элементов").max(1000, "Максимум 1000 элемеентов"),
})

export default Form;